.div {
  margin: 0px auto;
  position: relative;
  top: 160px;
  width: 50%;
  background-color: white;
}
.input {
  width: 80%;
  border: 0px transparent;
  height: 30px;
}
.span {
  width: 100%;
  background-color: aqua;
  height: 2px;
  margin-top: 20px;
  display: block;
}
.data_input {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px;
  padding-top: 14px;
}
.reset {
  border: transparent;
  padding: 10px 20px;
}
.search {
  border: transparent;
  padding: 10px 20px;
}
.reset:hover {
  background-color: aqua;
}
.search:hover {
  background-color: aqua;
}
.button {
  display: flex;
  justify-content: right;
  padding: 40px 20px;
  gap: 30px;
}
